bmit.password[0] }}</span\n                    >\n                </label>\n                <label class=\"form-grp-cstm\">\n                    Confirm Password\n                    <input name=\"password_confirmation\" type=\"password\" />\n                </label>\n                <button type=\"submit\">Reset Password</button>\n            </form>\n        </div>\n    </div>\n</template>\n\n<script>\nexport default {\n    props: [\"errorSubmit\", \"oldvalues\", \"url\", \"mail\", \"token\"],\n    data() {\n        return {\n            active: this.url ?? \"Host\",\n            csrf: $('meta[name=\"csrf-token\"]').attr(\"content\"),\n            email: this.mail ?? this.oldvalues[\"email\"] ?? null,\n        };\n    },\n    computed: {\n        formSubmitRoute() {\n            if (\n                this.url == \"host\" ||\n                this.url == \"bride\" ||\n                this.url == \"groom\" ||\n                this.url == \"member\"\n            ) {\n                return route(\"hostpassword.update\");\n            }\n            if (this.url == \"vendor\") {\n                return route(\"vendorpassword.update\");\n            }\n            if (this.url == \"guest\") {\n                return route(\"guestpassword.update\");\n            }\n            if (this.url == \"company\") {\n                return route(\"companypassword.update\");\n            }\n            if (this.url == \"admin\") {\n                return route(\"adminpassword.update\");\n            }\n        },\n    },\n};\n</script>\n\n<style scoped>\n.main-cont {\n    width: 100%;\n    height: 100%;\n    min-height: 80vh;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n.verify-box {\n    background: #ffffff;\n    border: 0.5px solid #e1e1e1;\n    border-radius: 16px;\n    max-width: 532px;\n    margin: 10px;\n    padding: 49px;\n}\n.verify-box > .top {\n    display: flex;\n}\n.top h1 {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 25px;\n    line-height: 38px;\n    color: #000000;\n}\n.top p {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 16px;\n    line-height: 24px;\n    color: #000000;\n}\n.top img {\n    width: 100px;\n    height: 100px;\n    align-self: flex-end;\n}\n.middle {\n    background: #e8f8e2;\n    border-radius: 5px;\n    padding: 16px;\n    font-family: \"Inter\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 15px;\n    line-height: 20px;\n    color: #3b682c;\n}\n.middle > a {\n    text-decoration: none;\n    display: block;\n    width: 100%;\n    color: inherit;\n}\n.form-grp-cstm {\n    font-family: \"Inter\";\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 20px;\n    color: #6b7280;\n    display: flex;\n    flex-direction: column;\n}\n.form-grp-cstm input {\n    background: #ffffff !important;\n    border: 0.896226px solid #e5e7eb !important;\n    box-shadow: 0px 0.896226px 1.79245px rgba(31, 41, 55, 0.08) !important;\n    border-radius: 3.58491px !important;\n    padding: 15px 7px;\n    margin-bottom: 5px;\n}\n.verify-box > form {\n    margin-top: 15px;\n}\n.verify-box > form > button {\n    font-family: \"Inter\";\n    font-style: normal;\n    font-weight: 400;\n    font-size: 13.3726px;\n    line-height: 19px;\n    color: #ffffff;\n    width: 162px;\n    height: 40.5px;\n    background: #461952;\n    border-radius: 6.39474px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    margin-left: auto;\n    margin-top: 12px;\n}\n</style>\n"],"sourceRoot":""}]);
